<!---->
<template>
  <div id="user-info-wrapper" v-if="currentSubNav">
    <ul class="subnav">
      <li
        v-for="(item, index) in subNavList"
        :class="currentSubNav.value === item.value ? 'active' : ''"
        @click="selectSubNav(item)"
      >
        {{ item.label }}
      </li>
    </ul>
    <div class="content-wrapper">
      <div class="content" v-show="currentSubNav.value === 'basic'">
        <img :src="userInfo.profilePhoto + imageMiddleWidth" class="profile-photo" v-if="userInfo && userInfo.profilePhoto" @click="dialogVisible = true">
        <img src="../../static/images/default.png" class="profile-photo" v-else @click="dialogVisible = true">
        <div class="user-info">
          <div>
            <span class="label"></span>
            <span class="value" v-if="userInfo">{{ userInfo.userName || userInfo.mobile }}</span>
            <i class="iconfont icon-bianji" @click="inputNewName"></i>
          </div>
          <div>
            <span class="label"></span>
            <span class="value" v-if="userInfo">{{ userInfo.mobile }}</span>
          </div>
          <!-- <div>
            <span class="label"></span>
            <span class="value" v-if="userInfo.email">{{ userInfo.email }}</span>
            <a href="javascript:;" class="link" v-else></a>
          </div> -->
          <div>
            <span class="label"></span>
            <a
              href="javascript:;"
              class="link"
              @click="$router.push({ name: 'User', query: { nav: $route.query.nav, subNav: 'modifyPassword' } })"
            >
              
            </a>
          </div>
        </div>
      </div>
      <div class="modify-password" v-if="currentSubNav.value === 'modifyPassword'">
        <el-form :model="modifyInfo" ref="modifyForm" label-width="100px" :rules="modifyRules">
          <el-form-item label="" prop="password">
            <el-input type="password" v-model="modifyInfo.password" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="verifyCode" class="input-wrapper verification-code">
            <el-input v-model="modifyInfo.verifyCode" placeholder="" style="width: 200px;"></el-input>
            <el-button v-show="isAvailabelBtn" @click="sendModifyCode"></el-button>
            <el-button v-show="!isAvailabelBtn" disabled class="disabled-button">{{ countTime }} </el-button>
          </el-form-item>
          <el-form-item>
            <el-button @click="confirm"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
    >
      <avatar-image></avatar-image>
    </el-dialog>
  </div>
</template>

<script>
import avatarImage from '@/components/avatarImage.vue'

export default {
  name: 'userInfo',
  components: {
    avatarImage
  },
  data () {
    // 
    let validatePassword = (rule, value, callback) => {
      if (!value) return callback(new Error(''))
      const charReg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/
      if (!charReg.test(value)) return callback(new Error('6-16+'))
      callback()
    }
    // 
    let validateNewPassword = (rule, value, callback) => {
      if (!value) return callback(new Error(''))
      validatePassword(rule, value, callback)
    }
    // 
    let validateIndentifyingCode = (rule, value, callback) => {
      if (!value) return callback(new Error(''))
      callback()
    }
    return {
      userInfo: null,
      isAvailabelBtn: true,
      countTime: 60,
      currentSubNav: null,
      modifyInfo: {
        customerId: null,
        password: null,
        verifyCode: null
      },
      dialogVisible: false,
      subNavList: [{
        value: 'basic',
        label: ''
      }, {
        value: 'modifyPassword',
        label: ''
      }],
      modifyRules: {
        password: [
          { validator: validateNewPassword, trigger: 'blur' }
        ],
        verifyCode: [
          { validator: validateIndentifyingCode, trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    avatar () {
      return this.$store.state.avatar
    }
  },
  watch: {
    '$route' () {
      this.setSubNav()
    },
    // 
    avatar () {
      if (this.avatar) {
        this.dialogVisible = false
        this.axios.post(this.api.modifyAvatar, this.avatar).then(res => {
          // 
          if (res.status === 200 && res.data.code < 300) {
            this.userInfo.profilePhoto = res.data.result.imagePath
            let personalInfo = JSON.parse(sessionStorage.getItem('personalInfo'))
            personalInfo.profilePhoto = res.data.result.imagePath
            sessionStorage.setItem('personalInfo', JSON.stringify(personalInfo))
            this.$message({
              message: '',
              type: 'success'
            })
          } else {
            this.$message({
              message: res.data.remark,
              type: 'error'
            })
          }
        })
      }
    }
  },
  created () {
    this.setSubNav()
    this.getUserInfo()
  },
  methods: {
    // 
    getUserInfo () {
      let personalInfo = sessionStorage.getItem('personalInfo')
      if (personalInfo) return this.userInfo = JSON.parse(personalInfo)
      this.axios.get(this.api.getUserInfo).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.userInfo = res.data.result
          sessionStorage.setItem('personalInfo', JSON.stringify(res.data.result))
        }
      })
    },
    // 
    setSubNav () {
      this.currentSubNav = this.subNavList.find(subNav => subNav.value === this.$route.query.subNav)
    },
    selectSubNav (item) {
      this.$router.push({ name: 'User', query: { nav: this.$route.query.nav, subNav: item.value } })
    },
    // 
    sendModifyCode (code) {
      // 
      let params = {
        mobile: this.userInfo.mobile,
        smsType: 'CHANGE_PWD'
      }
      this.axios.post(this.api.getCode, params).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          // 60
          this.isAvailabelBtn = false
          let id = setInterval(() => {
            this.countTime--
            if (this.countTime === 0) {
              clearTimeout(id)
              this.countTime = 60
              this.isAvailabelBtn = true
            }
          }, 1000)
        }
        if (res.status === 200 && res.data.code === '500') {
          this.$message.error(res.data.remark)
        }
      })
    },
    // 
    inputNewName () {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputPattern: /^\S/,
        inputErrorMessage: ''
      }).then(({ value }) => {
        this.modifyNickName(value)
      })
    },
    // 
    modifyNickName (newName) {
      let params = {
        userName: newName
      }
      this.axios.post(this.api.modifyNickName, params).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.$message({
            message: '',
            type: 'success'
          })
          this.userInfo.userName = newName
          sessionStorage.setItem('personalInfo', JSON.stringify(this.userInfo))
          sessionStorage.setItem('userInfo', JSON.stringify(this.userInfo))
          this.$store.dispatch('setUpdateName', true)
        } else {
          this.$message({
            message: res.data.remark,
            type: 'error'
          })
        }
      })
    },
    // 
    confirm () {
      this.$refs.modifyForm.validate(valid => {
        if (valid) {
          this.modifyInfo.costomerId = this.userInfo.costomerId
          this.axios.post(this.api.modifyPassword, this.modifyInfo).then(res => {
            if (res.status === 200 && res.data.code === 200) {
              this.$router.push({ name: 'User', query: { nav: 'userInfo', subNav: 'basic' }})
              this.modifyInfo = {
                customerId: null,
                password: null,
                verifyCode: null
              }
              this.$message({
                message: '',
                type: 'success'
              })
            } else {
              this.$message({
                message: res.data.remark,
                type: 'error'
              })
            }
          })
        }
      })
    }
  }
}
</script>

<style lang="stylus">
#user-info-wrapper
  width: 100%
  .subnav
    display: flex
    width: 100%
    height: 36px
    line-height: 36px
    border-left: 4px solid #7BBE2B
    border-bottom: 1px solid #DDD
    li
      margin-left: 2rem
      cursor: pointer
  .content-wrapper
    padding: 1rem 2rem
    .content
      display: flex
      .profile-photo
        width: 136px
        height: 136px
      .user-info
        margin-left: 20px
        & > div
          height: 30px
          line-height: 30px
          .label
            display: inline-block
            width: 50px
          .link
            color: #FF4E00
            text-decoration: underline
          i
            margin-left: 5px
            color: #FF4E00
            cursor: pointer
    .modify-password
      .el-form
        width: 400px
        .verification-code
          .el-form-item__content
            display: flex
            align-items: center
            input
              width: 180px
            button
              flex-shrink: 0
              width: 100px
              font-size: 14px
              color: #FFF
              background: #7BBE2B
              display: flex
              justify-content: center
              &.disabled-button
                background: #C1C1C1
                cursor: not-allowed
</style>


